html  {

    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden; 

}
@media (min-width: 344px) {

    .carousel_btn {
        margin-top: 0px;
    }

    .carousel_cap {
        font-size: 10pt;
    }
    .carousel_cap h1{
        font-size: 14pt;
    }
}

@media (min-width: 768px) {

    .carousel_cap {
        font-size: 13pt;
    }
    .carousel_cap h1{
        font-size: 22pt;
    }
}

@media (min-width: 998px) {

    .carousel_cap {
        font-size: 13pt;
    }
    .carousel_cap h1{
        font-size: 32pt;
    }
}

.carousel_btn {
    border: 1px solid red;
}

.btn:hover {
    border: 1px solid rgb(155, 0, 0);
}

.carousel_cap p {
    letter-spacing: 2px;
}

#carousel {
    margin-top: 57px;
}